import React from 'react';
import './weather.css';
import wind from './assets/wind_black.png';
import sun from './assets/sun_black.png';
import wet from './assets/humidity_black.png';
import moon from './assets/moon_black.png';
import no_net from './assets/no_net.png';


class App extends React.Component{

  constructor(props){

    super(props)

    this.interval = undefined;
    this.state = {
      gotAll:undefined,
      temperature : undefined,
      temp_max: undefined,
      temp_min: undefined,
      wind : undefined,
      humidity: undefined,
      weather_condition:undefined,
      sun_moon : undefined,
      day_night : undefined,
      date: undefined,
      day : undefined,
      hours : undefined,
      minutes: undefined,
      country: undefined,
      place: undefined,
    };

    this.days = ['Sunday','Monday','Tuesday','Wednesday','Thursday','Friday','Saturday'];
    this.months = ['January','February','March','April','May','June','July','August','September','October','November','December'];

  }

  async getWeather(latitude,longitude){
    var timestamp = Date.now()/1000;
    var d = new Date();
    var url= 'https://api.openweathermap.org/data/2.5/weather?lat=' + String(latitude) + '&lon='+ String(longitude) + '&units=metric&APPID=83c6ba4dd07d83514536821a8a51d6d5';
    const response  =  await fetch(url);
    const jsons = await response.json();
    // console.log(jsons);
    this.setState({temperature:jsons.main.temp});
    this.setState({temp_max:jsons.main.temp_max});
    this.setState({temp_min:jsons.main.temp_min});
    this.setState({wind:jsons.wind.speed});
    this.setState({humidity:jsons.main.humidity});
    this.setState({weather_condition:jsons.weather[0].description});
    this.setState({country:jsons.sys.country});
    this.setState({place:jsons.name});
    if(timestamp > jsons.sys.sunrise && timestamp < jsons.sys.sunset){
      this.setState({sun_moon:sun});
      this.setState({day_night:"Daytime"});
    }
    else{
      this.setState({day_night:"Night"});
      this.setState({sun_moon:moon});
    }

    this.setState({day:this.days[d.getDay()]});
    this.setState({date:String(d.getDate()) + " " + this.months[d.getMonth()] + " " + String(d.getUTCFullYear())});
    this.setState({gotAll:true});
    // console.log(this.state);

  }

  getPosition = () => {
    return new Promise(function (resolve, reject) {
      navigator.geolocation.getCurrentPosition(resolve, reject);
    });
  }

  componentDidMount(){
    // console.log();
 
    this.getPosition().then(response =>{
      this.getWeather(response.coords.latitude,response.coords.longitude)
    })
    .catch((err) => {
      this.setState({gotAll:false});
    });

    this.interval = setInterval(() => {
      var d =  new Date();
      this.setState({hours:d.getHours()});
      if(String(d.getMinutes()).length === 1){
        this.setState({minutes:"0"+String(d.getMinutes())});
      }
      else{
        this.setState({minutes:d.getMinutes()});
      }
      
      // console.log(this.state);
    }, 1000);

  }

  componentWillUnmount() {
    clearInterval(this.interval);
  }




  render(){
    if(this.state.gotAll === true){
      return (

        <div style={{display:"flex",flexDirection:"row"}}>

          <div style={{alignItems: "center",padding:"10px"}}>
            <header style={{fontSize: "50px",fontFamily:" Arial, Helvetica, sans-serif",color:"#6BAB90"}}>Open Weather Map API</header><br></br>
            <label>OpenWeatherMap API usage in this application -</label>
            <ol>
              <li>We are displaying wind-speed, humidity, part of the day, place, temperature, Forecast ,Time of present time zone and Geo-Location</li>
              <li>Co-ordinates of the location is required for getting the above details. <br></br> We access the Geo-Location of the user to get the co-rdinates.</li>
              <li>Run time permission is asked for the Geo-Location, and we pass the co-ordinates to the API.</li>
              <li>For more information on API - <a href="https://openweathermap.org/api" target="_blank" style={{textDecoration: "none"}}>OpenWeatherMap API</a></li>
            </ol>
          </div>

          <div style={{color:"black",display:"flex",flexDirection:"column",alignItems:"center"}}>
            <div style={{marginLeft:"150px"}}> 
              <div  style={{paddingRight:"10px",float:"left",marginLeft:"-100px",marginRight:"100px"}}>
                <img src= {wind} alt="wind" style={{height: "30px",width: "30px", marginBottom:"-8px",paddingRight:"5px"}} />
                <label className="wind_header">{this.state.wind} km/hr</label>
              </div>
              <div style={{display:"flex",flexDirection:"row"}}>
                <img src= {wet} alt="wind" style={{height: "25px",width: "25px", marginBottom:"-4px",paddingLeft:"15px",paddingRight:"7px"}} />
                <label className="wind_header">{this.state.humidity}%</label>
              </div>  

              <br></br>
              
              <div>
                <label className="wind_header">{this.state.day}, </label>
                <label className="wind_header">{this.state.date}</label>
              </div>

            </div>  

            <br></br>

            <div style={{display:"flex",marginLeft:"100px"}}>
              <label className="wind_header" >{this.state.day_night}, </label>
              <label className="wind_header" style={{marginLeft:"5px"}}> Forecast - {this.state.weather_condition}</label>
            </div>

            <br></br>

            <div style={{display:"flex",marginLeft:"90px"}}>
              <label className="wind_header">Place - {this.state.place},</label>
              <label className="wind_header" style={{marginLeft:"5px"}}>{this.state.country}</label>
            </div>

            <br></br>

            <div style={{marginLeft:"120px",display:"flex",flexDirection:"row",alignItems:"center"}}>
              <div style={{float:"left",marginRight:"50px"}}>
                <label className="main_header" style={{marginTop:"10px"}}>{this.state.hours}</label>
                <label className="blink">:</label>
                <label className="main_header">{this.state.minutes}</label>
              </div>
              <div style={{float:"right",marginRight:"10px",display:"flex"}}>
                <img src={this.state.sun_moon} alt="wind" id="day_night" style={{height: "70px",width: "70px", marginBottom:"-15px"}}/>
                <label className="main_header">{this.state.temperature}°C</label> <br/>
              </div> 
            </div>

            {/* <header className="wind_header" style={{fontSize:"22px"}}>{this.state.temp_max}°C (max)</header>
            <div className="weather_bar"></div>
            <header className="wind_header" style={{fontSize:"22px"}}>{this.state.temp_min}°C(min)</header> */}
          </div>
        </div>



      );
    }
    else{
      return(
        <div style={{marginLeft:"120px",display:"flex",flexDirection:"row",}}>
          <img src={no_net} alt="no_net" style={{marginLeft:"120px",height: "40px",width: "40px", paddingRight:"15px"}}/>
          <label className="main_header" style={{fontSize:"20px",marginTop:"  10px",color:"red"}}>Please Check your internet connection</label> 
        </div>
      );

    }

  }

}

export default App;
